export default {
  name: 'ViewBox',

  props: {
    codes: Object,
    isShowFullCode: Boolean,
  },

  data: () => ({
    JSON,
  }),

  computed: {
    code() {
      const { html, css, js } = this.codes;
      const wrapCSS = css ? `<style>${ css }</style>` : '';
      let wrapJS = js ? `<script>${ js }</sc` : '';
      wrapJS += js ? `ript>` : '';

      if(!html && !css && !js) return '';
      return `<!DOCTYPE html>
<html lang="ru">
<head>
  <meta charset="UTF-8">
  <meta name="viewport" content="width=device-width, initial-scale=1.0">
  ${ wrapCSS }
</head>
<body>
  ${ (html || '') + wrapJS }
</body>
</html>`
    },
  },
};
